const getApprovalItems = async () => {
  loading.value = true;
  try {
    const response = await axiosInstance.get('budget-approval-lists?requisition_type=budget&budget_id='+route.params.budget_id);
    items.value = response.data.map((item, index) => {
      let status = item.status;
      if (item.last_checked_in?.status?.id == 4 || item.last_checked_in?.status?.id == 14) {
        status = {
          name: item.last_checked_in.status.name,
          color: item.last_checked_in.status.color
        };
      }

      let awaits = item.awaiting_approval?.name || 'no one';
      if (item.last_checked_in?.status?.id == 4 || item.last_checked_in?.status?.id == 14) {
        awaits = 'no one';
      }

      let awaiter_role_id = item.awaiting_approval?.role_id || null;
      if (item.last_checked_in?.status?.id == 4 || item.last_checked_in?.status?.id == 14) {
        awaiter_role_id = null;
      }

      return {
        ...item,
        status,
        purpose: item.approval_group,
        type: item.requisition_type ?? item?.source,
        approval: '0' + item.id,
        approval_id: item.id,
        requisition: item?.requisition_id ?? item?.source_id,
        requester : item.requester?.name ?? item?.requester,
        requester_id : item.requester?.id,
        awaits,
        awaiter_role_id,
        vendor: item.vendor && item.vendor.name ? item.vendor.name : item.beneficiary,
        vendor_data: item.vendor,
        date : formatDate(item.date),
        created_at : item.date,
        last_checked: formatDate(item.last_checked_in?.approved_at),
        cost: item.items
          ? formatNumber(item.items.reduce(
              (sum, i) =>
                sum +
                ((i?.quantity || 0) *
                  (i.requisition_items?.rate || 0)),
              0
            ))
          : 0,
        approved_at: item.last_checked_in?.approved_at
          ? new Date(item.last_checked_in.approved_at).toLocaleString('en-GB', {
              year: 'numeric',
              month: '2-digit',
              day: '2-digit',
              hour: '2-digit',
              minute: '2-digit',
              second: '2-digit'
            })
          : null
      };
    });

    items.value.sort((a, b) => {
      const role = Number(user_role_id.value);
      const aMatch = a.awaiter_role_id === role;
      const bMatch = b.awaiter_role_id === role;

      if (aMatch && !bMatch) return -1;
      if (!aMatch && bMatch) return 1;
      return 0;
    });

  } catch (error) {
    showError(error.message);
    console.error(error);
  } finally {
    loading.value = false;
  }
};

const filteredData = computed(() => {
  let data = items.value;
  
  // Apply search filter
  if (filter.value) {
    data = data.filter(item => 
      Object.values(item).some(value => 
        String(value).toLowerCase().includes(filter.value.toLowerCase())
      )
    );
  }
  
  // Apply approval group filter
  if (approvalGroupFilter.value) {
    data = data.filter(item => 
      item.approval_group?.toLowerCase().includes(approvalGroupFilter.value)
    );
  }
  
  // Add sequential Sno after filtering
  return data.map((item, index) => ({
    ...item,
    Sno: index + 1
  }));
});

//  approval group filter
const approvalGroupFilter = ref('');

// method to filter by approval group
const filterByApprovalGroup = (groupType) => {
  if (approvalGroupFilter.value === groupType) {
    // If already filtered by this group, clear the filter
    approvalGroupFilter.value = '';
  } else {
    // Set new filter
    approvalGroupFilter.value = groupType;
  }
};

