const getApprovalChain = (row) => {
  approval.value = row
  requisitionItems.value = row.items.map((item, index) => ({
    ...item,
    order_item_id :  item?.requisition_items?.id,
    Sno: index + 1,
    item: item?.requisition_items?.remark,
    description: item?.requisition_items?.description + (item.description ? ' ( ' + item.description + ' )' : ''),
    rate: Number(item?.requisition_items?.rate).toLocaleString() + '/=',
    rate_original :item?.requisition_items?.rate,
    total: (item?.quantity * item?.requisition_items?.rate).toLocaleString() + '/=',
    selected:false,
    rate_original : item?.requisition_items?.rate,
    requesting_quantity : '',
    request_description : ''
  }));

  awaits.value = row.awaits
  approval_description.value = row.approval_description;
  approvalChain.value = (row.roles_sequence || []).slice();
  lastApprovalRole.value = (row.roles_sequence?.length > 0)
  ? row.roles_sequence[row.roles_sequence.length - 1].role_id
  : null;
  awaiter.value = row.awaiting_approval || {};
  awaiter_id.value = row.awaiting_approval?.role_id || null;
  item_id.value = row.id;
  item_date.value = row.date;
  requisition_source_item_id.value = row;
  item.value = row;
  localChecked.value = false;
};


const remark = ref('')
const approveRequest = async () => {
  const payload = {
    subcontractor_agreement_id : item.value.subcontractor_agreement_id,
    approval_group_id : route.params.group_id,
    status_id : status.value,
    approval_item_id: item_id.value,
    role_id: user_role_id.value,
    remark: remark.value,
    last_role: lastApprovalRole.value,
    requisition_type: requisition_source_item_id.value.requisition_type,
    project : requisition_source_item_id.value,
    requisition_items : requisition_source_item_id.value.items.map((item)=>({
      source_id: item.requisition_items.item_id,
      cost : item.requisition_items.quantity * item.requisition_items.rate,
      description : item.requisition_items.description,
      remark : "From LPO NO : " + item_id.value + " created on date " + item_date.value,
      remarker_id : JSON.parse(localStorage.getItem('user')).id,
      remarker : JSON.parse(localStorage.getItem('user')).name
    }))
  };


  await axiosInstance.post('approval-chains', payload);
  awaiter_id.value = '';
  remark.value = ''
  getApprovalItems();
  showMessage('data saved successfully');
};


const onApproveClick = async () => {

  try {
    spinner.value = true;
